.order-form {
    font-size: 13px;
    &__group {
        padding: 24px 16px;
        text-align: center;
        &:not(:last-of-type) {
            border-bottom: 1px solid $silver; } }
    &__row {
        display: flex;
        justify-content: space-between;
        flex-wrap: wrap;
        color: $black;
        &:not(:first-of-type) {
            margin-top: 17px; }
        &--submit {
            padding: 0 36px;
            margin-top: 24px; }
        &--highlighted {
            background-color: $lightYellow;
            padding: 17px 8px 10px;
            margin: 0 -8px 24px; } }
    &__text {
        text-transform: uppercase;
        font-weight: 500;
        line-height: 1;
        &--red {
            color: $red; }
        &--big {
            font-size: 16px; }
        &--full {
            flex: 0 0 100%;
            text-align: left;
            margin-top: 10px; }
        &-lower {
            font-size: 14px;
            display: inline-block;
            &--bold {
                font-weight: 500;
                min-width: 100px; } }
        &-dark {
            color: $gray; } }

    &__submit {
        margin-top: 24px;
        padding-left: 64px;
        padding-right: 64px;
        &--delivery {
            background-color: $gallery;
            color: $black;
            cursor: default;
            @include hover {
                background-color: $gallery; } } }
    &__card-group {
        .card {
            font-size: 14px;
            text-align: left;
            padding: 0;
            &__img {
                width: 140px;
                height: 140px; }
            &__wrap {
                padding-left: 20px;
                padding-bottom: 0; }
            &__vendorcode {
                font-size: 11px;
                color: $gray;
                margin-bottom: 4px; }
            &__title {
                font-size: 14px;
                line-height: 1.3; }
            &__quantity {
                font-size: 13px;
                line-height: 1.4;
                margin-top: {}
                margin-bottom: 8px; }
            &__benefit {
                font-size: {} } } }
    &__card-group {
        text-align: left; }
    &__more {
        text-decoration: underline;
        font-size: 14px;
        color: $gray;
        line-height: 1.5;
        margin: 15px auto;
        display: block; }
    &__divider {
        height: 1px;
        background-color: $silver;
        &--top {
            margin-top: 16px; }
        &--bottom {
            margin-bottom: 24px; } } }

.order-checkbox {
    margin-top: 16px;
    margin-bottom: 24px; }

.order-calculation {
    &__btn {
        border-bottom: 1px solid $silver;
        border-top: 1px solid $silver;
        width: 100%;
        text-transform: uppercase;
        vertical-align: top;
        display: flex;
        justify-content: space-between;
        align-items: center;
        font-size: 0;
        font-weight: 500;
        padding: 14px;
        background-color: transparent;
        position: relative;
        &:after,
        &:before {
            position: absolute;
            content: '';
            top: 100%;
            height: 100%;
            background-color: $silver; }
        &:after {
            right: 0; }
        &:before {
            left: 0; }

        span {
            font-size: 13px; }
        .icon {
            font-size: 20px;
            fill: $silver;
            transition: transform 0.4s; }
        &.is-open .icon {
            @include rotate(180); } }
    &__form {
        border: 1px solid $silver;
        border-top: none; } }


.delivery-calculation {
    input {
        margin-bottom: 16px; }
    &__filter {
        display: flex;
        align-items: center;
        padding: 14px 7px 10px 16px;
        justify-content: space-between; }

    &__city {
        font-size: 16px;
        color: $black;
        padding-left: 16px;
        margin-bottom: 16px; }
    &__input-group {
        padding: 24px 52px; }
    &__check-group {
        padding: 0 16px 0;
        margin-bottom: -16px; }
    .radio-tab {
        background-color: transparent;
        justify-content: flex-start;
        &__label {
            padding: 0 0 16px 28px;
            &.is-active {
                background-color: transparent; } }
        &__title {
            position: relative;
            font-size: 14px;
            margin-bottom: 0;
            color: $black;
            &:before {
                top: 0;
                left: -28px; }
            &:after {
                top: 4px;
                left: -24px; } }
        &__dscr {
            font-size: 14px;
            color: $black; } } }
